@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Cinzel:wght@400..900&display=swap");
* {
  font-family: "游ゴシック体", YuGothic, "游ゴシック", "Yu Gothic", sans-serif;
}

body {
  color: #3c3c3c;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.6em;
}

a img {
  opacity: 1;
  transition: opacity 0.4s;
}
a:hover img {
  opacity: 0.8;
  transition: all 0.6s;
}
a.opacity {
  opacity: 1;
  transition: opacity 0.4s;
}
a.opacity:hover {
  opacity: 0.8;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}

.center-block {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.center {
  text-align: center;
}

.flex {
  display: flex;
}

.f-wrap {
  flex-wrap: wrap;
}

.f-jc-sb {
  justify-content: space-between;
}

.f-jc-c {
  justify-content: center;
}

.f-jc-fe {
  justify-content: flex-end;
}

.f-ai-c {
  align-items: center;
}

.f-ai-fs {
  align-items: flex-start;
}

.f-ai-fe {
  align-items: flex-end;
}

.f-ac-c {
  align-content: center;
}

.base {
  width: 100%;
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
}

/* 共通部分
------------------------------------------------------------*/
a.another__tab {
  position: relative;
}
a.another__tab::after {
  position: absolute;
  content: "";
  top: 0;
  bottom: 0;
  margin: auto;
  background-size: contain;
  background-image: url(../../img/common/ico__another-tab.svg);
}

@media only screen and (min-width: 768px) {
  .sp {
    display: none !important;
  }
}
@media only screen and (max-width: 767px) {
  .pc {
    display: none !important;
  }
}
/* ヘッダーここから */
body.fixed {
  overflow: hidden;
}

header {
  font-family: "游ゴシック", "YuGothic", "ヒラギノ角ゴ Pro", "Hiragino Kaku Gothic Pro", "メイリオ", Meiryo, Osaka, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;
  position: sticky;
  top: 0;
  left: 0;
  z-index: 999;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 120px;
  padding: 0 0 0 46px;
  background: #fff;
}
@media screen and (max-width: 1050px) {
  header {
    height: 80px;
    padding: 0 0 0 20px;
  }
}
header .logo {
  width: 180px;
}
@media screen and (max-width: 1050px) {
  header .logo {
    width: 205px;
    display: flex;
    align-items: center;
    gap: 0 9px;
    position: relative;
    z-index: 990;
  }
  header .logo img {
    width: 132px;
  }
}
header .logo .page__ttl {
  display: none;
  padding: 4px 5px;
  border: solid 1px #3c3c3c;
  border-radius: 50px;
  font-size: 10px;
  transform-origin: left;
  transform: scale(0.8);
  font-weight: 700;
  color: #3c3c3c;
}
@media screen and (max-width: 1050px) {
  header .logo .page__ttl {
    display: block;
  }
}
header .header__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 570px;
  margin-right: 230px;
}
@media (max-width: 1050px) {
  header .header__list {
    max-width: 850px;
  }
}
@media screen and (max-width: 1050px) and (min-width: 1098px) {
  header .header__list {
    flex-wrap: wrap;
    max-width: none;
    margin-top: 80px;
    padding: 0 20px;
    background-color: #ffffff;
  }
}
header .header__list .link {
  position: relative;
}
@media only screen and (max-width: 1050px) {
  header .header__list .link {
    width: 100%;
    border-bottom: 1px solid #ecf2fb;
  }
}
@media only screen and (min-width: 1051px) {
  header .header__list .link:hover .txt::before {
    opacity: 1;
  }
}
@media only screen and (max-width: 1050px) {
  header .header__list .link:has(.accordion) .txt::before {
    content: "";
    position: absolute;
    top: 50%;
    right: 15px;
    transform: translateY(-50%) rotate(45deg);
    padding: 5px;
    border-right: 1px solid #fff;
    border-bottom: 1px solid #fff;
    transition: 0.2s;
  }
  header .header__list .link:has(.accordion) .txt::after {
    content: "";
    position: absolute;
    top: 60%;
    right: 15px;
    transform: translateY(-50%) rotate(45deg);
    padding: 5px;
    border-top: 1px solid #fff;
    border-left: 1px solid #fff;
    opacity: 0;
    transition: 0.2s;
  }
  header .header__list .link:has(.accordion) .txt.active::before {
    opacity: 0;
  }
  header .header__list .link:has(.accordion) .txt.active::after {
    opacity: 1;
  }
}
header .header__list .link .txt {
  display: block;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0.08em;
  line-height: 1.7em;
  padding: 20px 0;
  color: #1476cb;
}
header .header__list .link .txt .menu__txt {
  color: #3c3c3c;
}
@media only screen and (max-width: 1050px) {
  header .header__list .link .txt {
    padding: 25px 0;
    text-align: center;
    display: flex;
    justify-content: center;
  }
}
@media only screen and (min-width: 1051px) {
  header .header__list .link .txt::before {
    content: "";
    position: absolute;
    bottom: 20px;
    left: 0;
    width: 100%;
    height: 2px;
    opacity: 0;
    transition: 0.2s;
  }
}
header .header__list .link .ico__gnav__circle::before {
  right: -20px;
  width: 7px;
  height: 7px;
  border-top: 2px solid #1476cb;
  border-right: 2px solid #1476cb;
}
header .header__list .link .txt__cover {
  display: flex;
  flex-direction: column;
}
header .header__list .link .txt__cover .en {
  font-family: "游ゴシック", "YuGothic", "ヒラギノ角ゴ Pro", "Hiragino Kaku Gothic Pro", "メイリオ", Meiryo, Osaka, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;
  font-size: 22px;
  font-weight: 700;
  letter-spacing: 0.06em;
  line-height: 1em;
}
header .header__list .link .txt__cover .ja {
  display: block;
  position: relative;
  font-size: 10.9px;
  letter-spacing: 0.12em;
  line-height: 1.2em;
  padding: 0 0 0 18px;
}
header .header__list .link .txt__cover .ja::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 2px;
  transform: translate(0, -50%);
  z-index: 0;
  width: 11px;
  height: 1.5px;
  background: #fff;
}
header .header__list .link .object {
  position: relative;
  width: 64px;
  height: 64px;
  border-radius: 50%;
  display: grid;
  place-items: center;
}
header .header__list .link .object::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 45.47px;
  height: 45.47px;
  border: solid 1px #2e7dc0;
  border-radius: 50%;
}
header .header__list .link .object::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 25px;
  height: 25px;
  background: #fff;
  border-radius: 50%;
}
header .header__list .link .object .arrow {
  position: relative;
  z-index: 10;
  width: 7.5px;
  height: 9.5px;
  overflow: hidden;
  transform: translateX(1px);
}
header .header__list .link .object .arrow::before {
  content: "";
  position: absolute;
  top: 50%;
  right: 0%;
  transform: translate(-20%, -50%) rotate(45deg);
  z-index: 0;
  width: 10px;
  height: 10px;
}
header .header__list .link .object i {
  position: absolute;
  display: block;
  width: 25px;
  height: 25px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.4s all;
  border-radius: 50%;
}
header .header__list .link .object i::before {
  content: "";
  display: block;
  position: absolute;
  top: calc(42% - 25px);
  left: calc(42% - 25px);
  transform: scale(0);
  width: 50px;
  height: 50px;
  transition: 0.5s all;
  border: 2px solid #fff;
  border-radius: 50%;
  animation: scale 1.9s ease 0s infinite forwards;
  z-index: 99;
}
header .header__list .link .object i::after {
  content: "";
  display: block;
  position: absolute;
  top: calc(42% - 40px);
  left: calc(42% - 40px);
  transform: scale(0);
  width: 80px;
  height: 80px;
  transition: 0.5s all;
  border: 2px solid #fff;
  border-radius: 50%;
  animation: scale 1.9s ease 0.4s infinite forwards;
  z-index: 99;
}
@keyframes scale {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}
header .header__list .link.requirements a {
  display: block;
  padding: 7px 25px;
  border-radius: 50px;
  transition: all 0.4s ease;
}
header .header__list .link.simulation {
  top: 0;
  right: 0;
  position: absolute;
  width: 180px;
  height: 180px;
  background-color: #0c86e1;
}
header .header__list .link.simulation a {
  display: flex;
  row-gap: 16px;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  color: #fff;
}
header .header__list .link.simulation .link__txt {
  text-align: center;
  font-size: 12px;
  line-height: 1.5;
}
header .header__list .link.simulation .img__simulation {
  width: 38px;
}
header .header__list .link .accordion {
  display: none;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  min-width: 220px;
  background: #fff;
  padding: 30px 20px;
}
@media only screen and (max-width: 1050px) {
  header .header__list .link .accordion {
    position: relative;
    width: 100%;
    top: initial;
    left: initial;
    transform: translate(0);
    padding: 0;
  }
}
header .header__list .link .accordion__item {
  margin: 0 0 10px;
}
@media only screen and (max-width: 1050px) {
  header .header__list .link .accordion__item {
    margin: 0 0 0px;
  }
}
header .header__list .link .accordion__item:last-child {
  margin: 0;
}
@media only screen and (max-width: 1050px) {
  header .header__list .link .accordion__item:last-child {
    margin: 0 0 0px;
    padding: 0 0 10px;
  }
}
@media only screen and (max-width: 1050px) {
  header .header__list .link .accordion__item:first-child {
    padding: 10px 0 0;
  }
}
header .header__list .link .accordion__txt {
  position: relative;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0.08em;
  line-height: 1.7em;
  color: #3c3c3c;
  padding: 0 0 0 12px;
}
@media only screen and (max-width: 1050px) {
  header .header__list .link .accordion__txt {
    display: inline-block;
    padding: 10px 0 10px 12px;
    margin: 0 0 0 20px;
  }
}
header .header__list .link .accordion__txt::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 6px;
  height: 1px;
  background: #7f8080;
}
header .header__list02 {
  margin: 35px auto 0;
  max-width: 850px;
}
header .header__list02 .link {
  margin-bottom: 15px;
}
header .header__list02 .link .txt {
  font-size: 14px;
