}
header .header__menu {
  display: flex;
}
@media screen and (min-width: 1051px) {
  header .header__menu {
    display: none;
  }
}
header .header__menu .link a {
  width: 80px;
  height: 80px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  gap: 15px 0;
}
header .header__menu .link.requirements a {
  position: relative;
}
header .header__menu .link.requirements a::before {
  content: "";
  position: absolute;
  top: 54%;
  left: 50%;
  transform: translate(-50%, -90%);
  z-index: 0;
  width: 20px;
  height: 23px;
  background: url(../../img/common/icon_paper.svg) no-repeat 50% 50%/cover;
}
header .header__menu .link.entry a {
  position: relative;
  color: #fff;
}
header .header__menu .link.entry a::before {
  content: "";
  position: absolute;
  top: 54%;
  left: 50%;
  transform: translate(-50%, -90%);
  z-index: 0;
  width: 20px;
  height: 20px;
  background: url(../../img/common/icon_check_mark.svg) no-repeat 50% 50%/cover;
}
header .header__menu .link.menu {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 15px 0;
  width: 80px;
  height: 80px;
  position: relative;
  z-index: 999;
  background: #0c86e1;
  color: #fff;
  cursor: pointer;
}
header .header__menu .link.menu .line__box {
  position: relative;
  width: 30px;
  height: 13px;
}
header .header__menu .link.menu .line__box .menu__line {
  position: absolute;
  right: 0;
  background: #fff;
  height: 1px;
  transform: translateY(-50%);
  transition: all 0.4s;
  width: 30px;
}
header .header__menu .link.menu .line__box .menu__line:nth-child(1) {
  top: 0;
}
header .header__menu .link.menu .line__box .menu__line:nth-child(2) {
  top: 50%;
}
header .header__menu .link.menu .line__box .menu__line:nth-child(3) {
  top: 100%;
}
header .header__menu .link.menu.opened {
  background-color: rgba(255, 255, 255, 0);
}
header .header__menu .link.menu.opened .line__box {
  position: relative;
  width: 30px;
  height: 13px;
}
header .header__menu .link.menu.opened .line__box .menu__line {
  background-color: #1476cb;
}
header .header__menu .link.menu.opened .line__box .menu__line:nth-child(1) {
  transform: translate(-50%, -50%) rotate(45deg);
  right: initial;
  left: 50%;
  top: 50%;
}
header .header__menu .link.menu.opened .line__box .menu__line:nth-child(2) {
  right: -10px;
  opacity: 0;
}
header .header__menu .link.menu.opened .line__box .menu__line:nth-child(3) {
  right: -10px;
  opacity: 0;
}
header .header__menu .link.menu.opened .line__box .menu__line:nth-child(4) {
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
  opacity: 1;
}
header .header__menu .link.menu.opened .link__txt.menu {
  display: none;
}
header .header__menu .link.menu.opened .link__txt.close {
  display: block;
}
header .header__menu .link .link__txt {
  display: block;
  text-align: center;
  font-size: 10px;
  font-weight: 700;
  letter-spacing: 0.08em;
  line-height: 1em;
  transform: translateY(-8px);
  transition: 0.4s;
}
header .header__menu .link .link__txt.close {
  display: none;
}
